.add-new-screen-dialog .lc-add-new-screen {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.add-new-screen-dialog .lc-add-new-screen .item-content {
  width: 150px !important;
}
.add-new-screen-dialog .add-new-screen-explain {
  color: #6e6e6e;
  padding: 0 7px;
  margin-bottom: 10px;
}
.add-new-screen-dialog .add-new-screen-explain p {
  margin-bottom: 2px;
}
.add-new-screen-dialog .add-new-screen-footer {
  margin-top: 3px;
  border-top: 2px solid #272b34;
  display: flex;
  flex-direction: row-reverse;
}
.add-new-screen-dialog .add-new-screen-footer .lc-button {
  font-size: 12px;
  color: #a3a3a3;
  width: 85px;
  height: 30px;
  margin: 6px 5px 10px 0;
}
