.lc-console .console-head {
  border-bottom: 2px solid #2A2A2A96;
  display: flex;
  height: 64px;
  padding: 0 20px;
  align-items: center;
  background-color: #141414;
}
.lc-console .console-head .console-head-title {
  font-size: 21px;
  font-weight: 600;
  color: #28ffff;
}
.lc-console .console-body {
  display: flex;
}
.lc-console .console-body .console-list {
  width: 200px;
  height: calc(100vh - 64px);
  background-color: #141414;
  border-right: 1px solid #2A2A2A96;
}
.lc-console .console-body .console-list .console-list-item:hover {
  background-color: rgba(38, 56, 56, 0.84);
  cursor: pointer;
}
.lc-console .console-body .console-list .console-list-item {
  color: #c5c5c5;
  display: flex;
  padding: 5px 10px;
  font-size: 16px;
  height: 50px;
  line-height: 40px;
  transition: background-color 0.5s ease;
}
.lc-console .console-body .console-list .console-list-item .item-icon {
  margin-right: 15px;
  position: relative;
  top: -1px;
}
.lc-console .console-body .console-list .console-list-item .item-icon img {
  width: 18px;
}
.lc-console .console-body .console-content {
  width: calc(100vw - 200px);
  height: calc(100vh - 64px);
  background-color: #171717;
  padding: 15px;
  overflow-y: scroll;
}
.lc-console .console-body .console-content .content-body .project-list {
  display: flex;
  flex-wrap: wrap;
}
.lc-console .console-body .console-content .content-body .project-list .project-item {
  border: 2px solid #0f7ba7;
  margin-right: 20px;
  margin-bottom: 20px;
  background-color: #1e696a;
  border-radius: 5px;
  align-items: center;
  justify-content: center;
  display: flex;
  color: whitesmoke;
  position: relative;
  overflow: hidden;
  background-size: 100%;
  background-position: center center;
}
.lc-console .console-body .console-content .content-body .project-list .project-item .pro-list-content {
  display: flex;
  z-index: 1;
  flex-direction: column;
  align-items: center;
}
.lc-console .console-body .console-content .content-body .project-list .project-item .pro-list-content .pro-content-title {
  font-size: 18px;
  transform: translateY(20px);
  transition: transform 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.lc-console .console-body .console-content .content-body .project-list .project-item .pro-list-content .pro-content-operates {
  margin-top: 10px;
  display: flex;
  opacity: 0;
  transform: translateY(20px);
  transition: transform 0.5s cubic-bezier(0.68, -0.55, 0.265, 1.55), opacity 0.5s;
}
.lc-console .console-body .console-content .content-body .project-list .project-item .pro-list-content .pro-content-operates .operate-item {
  width: 30px;
  height: 30px;
  margin: 0 10px;
  background-color: rgba(0, 68, 88, 0.51);
  border: 1px solid #0f7ba7;
  border-radius: 15px;
  text-align: center;
}
.lc-console .console-body .console-content .content-body .project-list .project-item .pro-list-content .pro-content-operates .operate-item img {
  width: 16px;
  position: relative;
  top: 1px;
  left: 0.6px;
  pointer-events: none;
}
.lc-console .console-body .console-content .content-body .project-list .project-item .pro-content-footer {
  position: absolute;
  bottom: 5px;
  right: 10px;
  display: flex;
  align-items: center;
}
.lc-console .console-body .console-content .content-body .project-list .project-item .pro-content-footer .state-point {
  animation: fade 2s infinite;
  width: 5px;
  height: 5px;
  border-radius: 5px;
}
.lc-console .console-body .console-content .content-body .project-list .project-item .pro-content-footer .state-text {
  opacity: 0.7;
  transform-origin: center center;
  transform: scale(0.8);
}
.lc-console .console-body .console-content .content-body .project-list .project-item::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
}
.lc-console .console-body .console-content .content-body .project-list .project-item:hover::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  opacity: 0;
  transition: opacity 0.5s;
}
.lc-console .console-body .console-content .content-body .project-list .project-item:hover {
  background-size: 150%;
  transition: background-size 0.5s;
}
.lc-console .console-body .console-content .content-body .project-list .project-item:hover .pro-list-content .pro-content-title {
  transform: translateY(-10px);
}
.lc-console .console-body .console-content .content-body .project-list .project-item:hover .pro-list-content .pro-content-operates {
  transform: translateY(0);
  opacity: 1;
}
.lc-console .console-body .console-content .content-body .project-list .project-item:not(:hover) {
  background-size: 100%;
  transition: background-size 0.5s;
}
.lc-console .console-body .console-content .content-body .project-list .project-item:hover::before {
  opacity: 1;
}
.lc-console .lc-new-screen-dialog .lc-new-screen-item {
  display: flex;
}
@keyframes fade {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.4;
  }
}
